@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  overflow-x: hidden;
}

.main-module {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  font-family: sans-serif, "Roboto";
}
.main-module-image__container {
  width: 100%;
  height: 50%;
}
.main-module-content__container {
  width: 100%;
  height: 50%;
  padding-top: 46px;
}

.content {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.content h1 {
  font-size: 42px;
  line-height: 50.4px;
  font-weight: 700;
}
.content p {
  max-width: 600px;
  padding: 32px;
  line-height: 24px;
}

.displayed-picture {
  width: 100%;
  height: 100%;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

@media (min-width: 768px) {
  .main-module-image__container {
    display: flex;
    justify-content: center;
    padding-top: 96px;
  }
  .content p {
    max-width: 720px;
  }
  .displayed-picture {
    width: auto;
  }
}
@media (min-width: 1140px) {
  .main-module {
    flex-direction: initial;
    max-width: 1440px;
  }
  .main-module-image__container {
    justify-content: flex-end;
    width: 50%;
  }
  .main-module-content__container {
    width: 50%;
    padding-top: 136px;
    padding-left: 95px;
  }
  .displayed-picture {
    width: 505px;
    height: 330px;
  }
  .content {
    width: auto;
    max-width: 380px;
    align-items: initial;
  }
  .content h1 {
    font-size: 42px;
    line-height: 50.4px;
    padding-bottom: 24px;
  }
  .content p {
    font-size: 16px;
    padding: 0;
    padding-bottom: 32px;
    line-height: 24px;
  }
}
.overlay-container {
  position: fixed;
  z-index: 5;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.overlay-container__backdrop {
  width: 100%;
  height: 100vh;
  position: fixed;
  background-color: #121212;
  opacity: 0.5;
}

.message-box__container {
  position: relative;
  max-height: 365px;
  z-index: 7;
  background-color: #FFFFFF;
  width: auto;
  overflow: hidden;
  font-family: sans-serif, "Roboto";
}

.close-burger {
  position: absolute;
  right: 46px;
  top: 46px;
  width: 12px;
  height: 12px;
  color: #000000;
  cursor: pointer;
}
.close-burger:before, .close-burger:after {
  position: absolute;
  left: 5px;
  content: "";
  height: 12px;
  width: 2px;
  background-color: #000000;
}
.close-burger:before {
  transform: rotate(45deg);
  height: 17px;
}
.close-burger:after {
  transform: rotate(-45deg);
  height: 17px;
}

.message-box__content {
  display: flex;
  justify-content: space-between;
  align-items: self-end;
  padding: 57px 25px;
}

.resetButtonVisible {
  display: block !important;
}

.message-box__text {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.message-box__text h2 {
  width: 100%;
  max-width: 380px;
  color: #121212;
  font-size: 42px;
}
.message-box__text p {
  color: #121212;
  line-height: 24px;
}
.message-box__text span {
  font-weight: 600;
}

@media (min-width: 768px) {
  .message-box__container {
    width: 720px;
  }
}
@media (min-width: 1140px) {
  .message-box__container {
    width: 920px;
  }
  .message-box__content {
    padding-left: 95px;
    padding-right: 95px;
  }
}
.btn {
  word-break: normal;
  text-decoration: none;
  padding: 16px 40px;
  display: inline-block;
  border-radius: 48px;
  border: none;
  text-align: center;
  transition: all 0.2s;
  position: relative;
  font-weight: 700;
  cursor: pointer;
}

.btn-not-visible {
  display: none;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn-colors {
  background-color: #E4C1AE;
  color: #FFFFFF;
}

.btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.4s;
}

.btn-colors::after {
  background-color: #A0887C;
}

.btn:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
.user-data-table__container {
  width: 100%;
  height: 140px;
  overflow-y: scroll;
}
.user-data-table__container table, .user-data-table__container thead, .user-data-table__container tbody, .user-data-table__container th, .user-data-table__container td, .user-data-table__container tr {
  display: block;
}
.user-data-table__container table {
  border-collapse: collapse;
  width: 100%;
}
.user-data-table__container table th {
  background-color: #EEEEEE;
}
.user-data-table__container table th, .user-data-table__container table td {
  border: 1px solid #DDDDDD;
  text-align: left;
  padding: 8px;
  text-align: center;
}
.user-data-table__container table td {
  position: relative;
  padding-left: 50%;
}
.user-data-table__container table tr:nth-child(even) {
  background-color: #EEEEEE;
}
.user-data-table__container table thead tr {
  position: absolute;
  top: -9999px;
  left: -9999px;
}
.user-data-table__container table td:before {
  position: absolute;
  top: 0;
  left: 6px;
  width: 45%;
  padding: 8px;
  padding-right: 10px;
  white-space: nowrap;
  font-weight: 700;
}
.user-data-table__container table td:nth-of-type(1):before {
  content: "First & Last Name";
}
.user-data-table__container table td:nth-of-type(2):before {
  content: "Email";
}
.user-data-table__container table td:nth-of-type(3):before {
  content: "City";
}
.user-data-table__container table td:nth-of-type(4):before {
  content: "Street";
}
.user-data-table__container table td:nth-of-type(5):before {
  content: "Suite";
}
.user-data-table__container table td:nth-of-type(6):before {
  content: "Mobile Phone";
}
.user-data-table__container table td:nth-of-type(7):before {
  content: "Company Name";
}

@media (min-width: 1140px) {
  .user-data-table__container table {
    display: table;
  }
  .user-data-table__container table thead {
    display: table-header-group;
  }
  .user-data-table__container table thead tr {
    position: relative;
    left: initial;
    top: initial;
  }
  .user-data-table__container table tbody {
    display: table-row-group;
  }
  .user-data-table__container table tr {
    display: table-row;
  }
  .user-data-table__container table th, .user-data-table__container table td {
    display: table-cell;
  }
  .user-data-table__container table td {
    border: initial;
    border-bottom: 1px solid #EEEEEE;
    padding: 8px !important;
  }
  .user-data-table__container table td:nth-of-type(1):before {
    content: none;
  }
  .user-data-table__container table td:nth-of-type(2):before {
    content: none;
  }
  .user-data-table__container table td:nth-of-type(3):before {
    content: none;
  }
  .user-data-table__container table td:nth-of-type(4):before {
    content: none;
  }
  .user-data-table__container table td:nth-of-type(5):before {
    content: none;
  }
  .user-data-table__container table td:nth-of-type(6):before {
    content: none;
  }
  .user-data-table__container table td:nth-of-type(7):before {
    content: none;
  }
}
.loader-not-visible {
  display: none;
}

.loading-screen-overlay {
  background: rgba(0, 0, 0, 0.9);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  z-index: 8;
}

.spinner {
  font-size: 75px;
  position: relative;
  display: inline-block;
  width: 1em;
  height: 1em;
}
.spinner.center {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  z-index: 8;
}

.spinner-blade {
  position: absolute;
  left: 0.4629em;
  bottom: 0;
  width: 0.074em;
  height: 0.2777em;
  border-radius: 0.5em;
  background-color: transparent;
  transform-origin: center -0.2222em;
  animation: spinner-fade 1s infinite linear;
}
.spinner-blade:nth-child(1) {
  animation-delay: 0s;
  transform: rotate(0deg);
}
.spinner-blade:nth-child(2) {
  animation-delay: 0.083s;
  transform: rotate(30deg);
}
.spinner-blade:nth-child(3) {
  animation-delay: 0.166s;
  transform: rotate(60deg);
}
.spinner-blade:nth-child(4) {
  animation-delay: 0.249s;
  transform: rotate(90deg);
}
.spinner-blade:nth-child(5) {
  animation-delay: 0.332s;
  transform: rotate(120deg);
}
.spinner-blade:nth-child(6) {
  animation-delay: 0.415s;
  transform: rotate(150deg);
}
.spinner-blade:nth-child(7) {
  animation-delay: 0.498s;
  transform: rotate(180deg);
}
.spinner-blade:nth-child(8) {
  animation-delay: 0.581s;
  transform: rotate(210deg);
}
.spinner-blade:nth-child(9) {
  animation-delay: 0.664s;
  transform: rotate(240deg);
}
.spinner-blade:nth-child(10) {
  animation-delay: 0.747s;
  transform: rotate(270deg);
}
.spinner-blade:nth-child(11) {
  animation-delay: 0.83s;
  transform: rotate(300deg);
}
.spinner-blade:nth-child(12) {
  animation-delay: 0.913s;
  transform: rotate(330deg);
}

@keyframes spinner-fade {
  0% {
    background-color: #69717D;
  }
  100% {
    background-color: transparent;
  }
}